type Framework = 'angular' | 'react' | 'vue' | 'vanilla-js' |'shared';
type FrameworkMarkup = { [key in Framework]?: string };

type ComponentsReadySampleName = 'default' | 'testbed' | 'testing-library';
export const getComponentsReadyCodeSamples = (sampleName: ComponentsReadySampleName): FrameworkMarkup => {
  const samples: { [key in ComponentsReadySampleName]: { [key in Framework]?: string } } = {"default":{"vanilla-js":"import { componentsReady } from '@porsche-design-system/components-js';\nimport { getByRoleShadowed } from '@porsche-design-system/components-js/testing';\nimport userEvent from '@testing-library/user-event';\n\nconst getMarkup = (): string => {\n  return `<p-button>Button 1</p-button>`;\n};\n\nit('should return 0 when nothing is rendered', async () => {\n  document.body.innerHTML = '';\n  expect(await componentsReady()).toBe(0);\n});\n\nit('should return 1 after component is rendered initially', async () => {\n  document.body.innerHTML = getMarkup();\n  expect(document.body.innerHTML).toEqual('<p-button>Button 1</p-button>');\n\n  expect(await componentsReady()).toBe(1);\n  const el = document.body.firstElementChild;\n  expect(el.shadowRoot).not.toBeNull();\n  expect(el.className).toBe('hydrated');\n});\n\nit('should return 2 after button is clicked', async () => {\n  document.body.innerHTML = getMarkup();\n  expect(await componentsReady()).toBe(1);\n\n  const scriptEl = document.createElement('script');\n  scriptEl.textContent = `\ndocument.querySelector('p-button').addEventListener('click', () => {\n  const el = document.createElement('p-button');\n  el.innerText = 'Button 2';\n  document.body.append(el);\n});`;\n  document.body.append(scriptEl);\n\n  const button = getByRoleShadowed('button');\n  await userEvent.click(button);\n\n  expect(await componentsReady()).toBe(2);\n});","angular":"import { Component } from '@angular/core';\nimport { TestBed, waitForAsync } from '@angular/core/testing';\nimport { componentsReady, PorscheDesignSystemModule } from '@porsche-design-system/components-angular';\nimport { By } from '@angular/platform-browser';\n\n@Component({\n  selector: 'empty',\n  template: `<div></div>`,\n})\nclass EmptyComponent {}\n\n@Component({\n  selector: 'sample',\n  template: `\n    <p-button (click)=\"onClick()\">Button 1</p-button>\n    <p-button *ngIf=\"active\">Button 2</p-button>\n  `,\n})\nclass SampleComponent {\n  active = false;\n  onClick() {\n    this.active = true;\n  }\n}\n\nconst replaceHtmlComments = (input: string): string => input.replace(/<!--[\\s\\S]+?-->/g, '');\n\nbeforeEach(waitForAsync(() => {\n  TestBed.configureTestingModule({\n    imports: [PorscheDesignSystemModule],\n    declarations: [EmptyComponent, SampleComponent],\n  }).compileComponents();\n}));\n\nit('should return 0 when nothing is rendered', async () => {\n  // we need to create something to bootstrap the design system via PorscheDesignSystemModule, which is calling load()\n  TestBed.createComponent(EmptyComponent);\n\n  expect(await componentsReady()).toBe(0);\n});\n\nit('should return 1 after component is rendered initially', async () => {\n  const fixture = TestBed.createComponent(SampleComponent);\n  expect(replaceHtmlComments(fixture.nativeElement.innerHTML)).toEqual('<p-button>Button 1</p-button>');\n\n  expect(await componentsReady()).toBe(1);\n  expect(replaceHtmlComments(fixture.nativeElement.innerHTML)).toEqual(\n    '<p-button class=\"hydrated\">Button 1</p-button>'\n  );\n});\n\nit('should return 2 after button is clicked', async () => {\n  const fixture = TestBed.createComponent(SampleComponent);\n  await componentsReady();\n\n  const button = fixture.debugElement.query(By.css('p-button')).nativeElement.shadowRoot.querySelector('button');\n  button.click();\n  fixture.detectChanges();\n\n  expect(await componentsReady()).toBe(2);\n});","react":"import { useState } from 'react';\nimport { componentsReady, PButton } from '@porsche-design-system/components-react';\nimport { render } from '@testing-library/react';\nimport userEvent from '@testing-library/user-event';\nimport { getByRoleShadowed } from '@porsche-design-system/components-react/testing';\nimport '@porsche-design-system/components-react/jsdom-polyfill';\n\nconst Sample = (): JSX.Element => {\n  const [active, setActive] = useState(false);\n\n  return (\n    <>\n      <PButton onClick={() => setActive(true)}>Button 1</PButton>\n      {active && <PButton>Button 2</PButton>}\n    </>\n  );\n};\n\nbeforeAll(() => {\n  (window as any).PDS_SKIP_FETCH = true;\n});\n\nit('should return 0 when nothing is rendered', async () => {\n  expect(await componentsReady()).toBe(0);\n});\n\nit('should return 1 after component is rendered initially', async () => {\n  const { container } = render(<Sample />);\n  expect(container.innerHTML).toEqual('<p-button>Button 1</p-button>');\n\n  expect(await componentsReady()).toBe(1);\n  expect(container.innerHTML).toEqual('<p-button class=\"hydrated\">Button 1</p-button>');\n});\n\nit('should return 2 after button is clicked', async () => {\n  render(<Sample />);\n  expect(await componentsReady()).toBe(1);\n\n  const button = getByRoleShadowed('button');\n  await userEvent.click(button);\n\n  expect(await componentsReady()).toBe(2);\n});"},"testbed":{"angular":"import { Component } from '@angular/core';\nimport { TestBed, waitForAsync } from '@angular/core/testing';\nimport { componentsReady } from '@porsche-design-system/components-angular';\nimport { By } from '@angular/platform-browser';\nimport { CommonModule } from '@angular/common';\nimport '@porsche-design-system/components-angular/jsdom-polyfill';\n\n@Component({\n  selector: 'empty',\n  template: '<div></div>',\n})\nclass EmptyComponent {}\n\n@Component({\n  selector: 'sample',\n  template: `\n    <p-button (click)=\"onClick()\">Button 1</p-button>\n    <p-button *ngIf=\"active\">Button 2</p-button>\n  `,\n})\nclass SampleComponent {\n  active = false;\n\n  onClick() {\n    this.active = true;\n  }\n}\n\nconst replaceHtmlComments = (input: string): string => input.replace(/<!--[\\s\\S]+?-->/g, '');\n\nbeforeAll(() => {\n  (window as any).PDS_SKIP_FETCH = true;\n});\n\nbeforeEach(waitForAsync(() => {\n  return TestBed.configureTestingModule({\n    imports: [CommonModule],\n    declarations: [EmptyComponent, SampleComponent],\n  }).compileComponents();\n}));\n\nit('should return 0 when nothing is rendered', async () => {\n  TestBed.createComponent(EmptyComponent);\n\n  expect(await componentsReady()).toBe(0);\n});\n\nit('should return 1 after component is rendered initially', async () => {\n  const fixture = TestBed.createComponent(SampleComponent);\n  expect(replaceHtmlComments(fixture.nativeElement.innerHTML)).toEqual('<p-button>Button 1</p-button>');\n\n  expect(await componentsReady()).toBe(1);\n  expect(replaceHtmlComments(fixture.nativeElement.innerHTML)).toEqual(\n    '<p-button class=\"hydrated\">Button 1</p-button>'\n  );\n});\n\nit('should return 2 after button is clicked', async () => {\n  const fixture = TestBed.createComponent(SampleComponent);\n  await componentsReady();\n\n  const button = fixture.debugElement.query(By.css('p-button')).nativeElement.shadowRoot.querySelector('button');\n  button.click();\n  fixture.detectChanges();\n\n  expect(await componentsReady()).toBe(2);\n});"},"testing-library":{"angular":"import { Component } from '@angular/core';\nimport { componentsReady } from '@porsche-design-system/components-angular';\nimport { getByRoleShadowed } from '@porsche-design-system/components-angular/testing';\nimport { render, fireEvent } from '@testing-library/angular';\nimport '@porsche-design-system/components-angular/jsdom-polyfill';\n\n@Component({\n  selector: 'empty',\n  template: `<div></div>`,\n})\nclass EmptyComponent {}\n\n@Component({\n  selector: 'sample',\n  template: `\n    <p-button (click)=\"onClick()\">Button 1</p-button>\n    <p-button *ngIf=\"active\">Button 2</p-button>\n  `,\n})\nclass SampleComponent {\n  active = false;\n  onClick() {\n    this.active = true;\n  }\n}\n\nconst replaceHtmlComments = (input: string): string => input.replace(/<!--[\\s\\S]+?-->/g, '');\n\nbeforeAll(() => {\n  (window as any).PDS_SKIP_FETCH = true;\n});\n\nit('should return 0 when nothing is rendered', async () => {\n  await render(EmptyComponent);\n\n  expect(await componentsReady()).toBe(0);\n});\n\nit('should return 1 after component is rendered initially', async () => {\n  const { container } = await render(SampleComponent);\n  expect(replaceHtmlComments(container.innerHTML)).toEqual('<p-button>Button 1</p-button>');\n\n  expect(await componentsReady()).toBe(1);\n  expect(replaceHtmlComments(container.innerHTML)).toEqual('<p-button class=\"hydrated\">Button 1</p-button>');\n});\n\nit('should return 2 after button is clicked', async () => {\n  await render(SampleComponent);\n  await componentsReady();\n\n  const button = getByRoleShadowed('button');\n  fireEvent.click(button);\n\n  expect(await componentsReady()).toBe(2);\n});"}};
  return samples[sampleName];
};

export const getAccordionCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"vanilla-js":"<p-accordion heading=\"Some Heading\" heading-tag=\"h3\">\n  <p-text>\n    Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore\n    magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.\n  </p-text>\n</p-accordion>\n<p-accordion heading=\"Some Heading\" heading-tag=\"h3\">\n  <p-text>\n    Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore\n    magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.\n  </p-text>\n</p-accordion>\n\n<script>\n  const accordions = document.querySelectorAll('p-accordion');\n  accordions.forEach((accordion) =>\n    accordion.addEventListener('update', (e) => {\n      e.target.open = e.detail.open;\n    })\n  );\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\nimport type { AccordionUpdateEventDetail } from '@porsche-design-system/components-angular';\n\n@Component({\n  selector: 'page-accordion-example',\n  template: `\n    <p-accordion [heading]=\"'Some Heading'\" [heading-tag]=\"'h3'\" [open]=\"isOpen1\" (update)=\"onUpdate1($event)\">\n      <p-text>{{ content }}</p-text>\n    </p-accordion>\n    <p-accordion [heading]=\"'Some Heading'\" [heading-tag]=\"'h3'\" [open]=\"isOpen2\" (update)=\"onUpdate2($event)\">\n      <p-text>{{ content }}</p-text>\n    </p-accordion>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class AccordionExampleComponent {\n  isOpen1 = false;\n  isOpen2 = false;\n  content =\n    'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et ' +\n    'dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.';\n\n  onUpdate1(e: CustomEvent<AccordionUpdateEventDetail>) {\n    this.isOpen1 = e.detail.open;\n  }\n  onUpdate2(e: CustomEvent<AccordionUpdateEventDetail>) {\n    this.isOpen2 = e.detail.open;\n  }\n}","react":"import { useCallback, useState } from 'react';\nimport { type AccordionUpdateEventDetail, PAccordion, PText } from '@porsche-design-system/components-react';\n\nexport const AccordionExamplePage = (): JSX.Element => {\n  const [isOpen1, setIsOpen1] = useState<boolean>(false);\n  const [isOpen2, setIsOpen2] = useState<boolean>(false);\n\n  const onUpdate1 = useCallback((e: CustomEvent<AccordionUpdateEventDetail>) => {\n    setIsOpen1(e.detail.open);\n  }, []);\n  const onUpdate2 = useCallback((e: CustomEvent<AccordionUpdateEventDetail>) => {\n    setIsOpen2(e.detail.open);\n  }, []);\n\n  const content =\n    'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et ' +\n    'dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.';\n\n  return (\n    <>\n      <PAccordion heading=\"Some Heading\" headingTag=\"h3\" open={isOpen1} onUpdate={onUpdate1}>\n        <PText>{content}</PText>\n      </PAccordion>\n      <PAccordion heading=\"Some Heading\" headingTag=\"h3\" open={isOpen2} onUpdate={onUpdate2}>\n        <PText>{content}</PText>\n      </PAccordion>\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { type AccordionUpdateEventDetail, PAccordion, PText } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const isOpen1 = ref(false);\n  const isOpen2 = ref(false);\n\n  const onUpdate1 = (e: AccordionUpdateEventDetail): void => {\n    isOpen1.value = e.open;\n  };\n  const onUpdate2 = (e: AccordionUpdateEventDetail): void => {\n    isOpen2.value = e.open;\n  };\n\n  const content =\n    'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et ' +\n    'dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.';\n</script>\n\n<template>\n  <PAccordion :heading=\"'Some Heading'\" :headingTag=\"'h3'\" :open=\"isOpen1\" @update=\"onUpdate1\">\n    <PText>{{ content }}</PText>\n  </PAccordion>\n  <PAccordion :heading=\"'Some Heading'\" :headingTag=\"'h3'\" :open=\"isOpen2\" @update=\"onUpdate2\">\n    <PText>{{ content }}</PText>\n  </PAccordion>\n</template>"};
  return samples;
};

export const getBannerCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"vanilla-js":"<p-button type=\"button\">Open Banner</p-button>\n<p-banner open=\"false\" heading=\"Some Heading\" heading-tag=\"h3\" description=\"Some Description\"></p-banner>\n\n<script>\n  const banner = document.querySelector('p-banner');\n  banner.addEventListener('dismiss', () => {\n    banner.open = false;\n  });\n  document.querySelector('p-button').addEventListener('click', () => (banner.open = true));\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-banner-example',\n  template: `\n    <p-button type=\"button\" (click)=\"onOpen()\">Open Banner</p-button>\n    <p-banner\n      [open]=\"isBannerOpen\"\n      [heading]=\"'Some Heading'\"\n      [heading-tag]=\"'h3'\"\n      [description]=\"'Some Description'\"\n      (dismiss)=\"onDismiss()\"\n    >\n    </p-banner>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class BannerExampleComponent {\n  isBannerOpen = false;\n\n  onOpen() {\n    this.isBannerOpen = true;\n  }\n  onDismiss() {\n    this.isBannerOpen = false;\n  }\n}","react":"import { useCallback, useState } from 'react';\nimport { PButton, PBanner } from '@porsche-design-system/components-react';\n\nexport const BannerExamplePage = (): JSX.Element => {\n  const [isBannerOpen, setIsBannerOpen] = useState(false);\n\n  const onOpen = useCallback(() => {\n    setIsBannerOpen(true);\n  }, []);\n\n  const onDismiss = useCallback(() => {\n    setIsBannerOpen(false);\n  }, []);\n\n  return (\n    <>\n      <PButton type=\"button\" onClick={onOpen}>\n        Open Banner\n      </PButton>\n      <PBanner\n        open={isBannerOpen}\n        heading=\"Some Heading\"\n        headingTag=\"h3\"\n        description=\"Some Description\"\n        onDismiss={onDismiss}\n      />\n    </>\n  );\n};"};
  return samples;
};

export const getButtonCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"vanilla-js":"<form>\n  <p-button name=\"option\" value=\"A\" type=\"submit\">Button A</p-button>\n  <p-button name=\"option\" value=\"B\" type=\"submit\">Button B</p-button>\n</form>\n\n<p-text>Last submitted data: none</p-text>\n\n<script>\n  const debugElement = document.querySelector('p-text');\n  const form = document.querySelector('form');\n  form.addEventListener('submit', (e) => {\n    e.preventDefault();\n    const formData = Array.from(new FormData(e.target, e.submitter).entries())[0];\n    debugElement.innerText = `Last submitted data: ${formData.join('=') || 'none'}`;\n  });\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-button-example-form',\n  template: `\n    <form (submit)=\"handleSubmit($event)\">\n      <p-button name=\"option\" value=\"A\" type=\"submit\">Button A</p-button>\n      <p-button name=\"option\" value=\"B\" type=\"submit\">Button B</p-button>\n    </form>\n\n    <p-text>Last submitted data: {{ lastSubmittedData }}</p-text>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class ButtonExampleFormComponent {\n  lastSubmittedData: string = 'none';\n\n  handleSubmit(e: Event): void {\n    e.preventDefault();\n    const submitter = (e as SubmitEvent).submitter as HTMLButtonElement;\n    this.lastSubmittedData = submitter.name + '=' + submitter.value || 'none';\n  }\n}","react":"import { type FormEvent, useState } from 'react';\nimport { PButton, PText } from '@porsche-design-system/components-react';\n\nexport const ButtonExampleFormPage = (): JSX.Element => {\n  const [lastSubmittedData, setLastSubmittedData] = useState('none');\n\n  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {\n    e.preventDefault();\n    const formData = Array.from(new FormData(e.currentTarget, (e.nativeEvent as SubmitEvent).submitter).entries())[0];\n    setLastSubmittedData(formData.join('=') || 'none');\n  };\n\n  return (\n    <>\n      <form onSubmit={handleSubmit}>\n        <PButton name=\"option\" value=\"A\" type=\"submit\">\n          Button A\n        </PButton>\n        <PButton name=\"option\" value=\"B\" type=\"submit\">\n          Button B\n        </PButton>\n      </form>\n\n      <PText>Last submitted data: {lastSubmittedData}</PText>\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { PButton, PText } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const lastSubmittedData = ref('none');\n  const handleSubmit = (e: Event) => {\n    const formData = Array.from(new FormData(e.target as HTMLFormElement, (e as SubmitEvent).submitter).entries())[0];\n    lastSubmittedData.value = formData.join('=') || 'none';\n  };\n</script>\n\n<template>\n  <form @submit.prevent=\"handleSubmit\">\n    <PButton name=\"option\" value=\"A\" type=\"submit\">Button A</PButton>\n    <PButton name=\"option\" value=\"B\" type=\"submit\">Button B</PButton>\n  </form>\n\n  <PText>Last submitted data: {{ lastSubmittedData }}</PText>\n</template>"};
  return samples;
};

export const getCanvasCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"vanilla-js":"<p-canvas sidebar-start-open=\"false\" sidebar-start-width=\"medium\" sidebar-end-open=\"false\" sidebar-end-width=\"medium\">\n  <div slot=\"header\">\n    <p-tag color=\"background-base\">Header</p-tag>\n    <p-button-pure id=\"open-sidebar-start\" icon=\"menu-lines\">Toggle Sidebar Start</p-button-pure>\n    <p-button-pure id=\"open-sidebar-end\" icon=\"menu-lines\">Toggle Sidebar End</p-button-pure>\n  </div>\n\n  <div class=\"module\">\n    <div class=\"tile tile--full\">Full</div>\n  </div>\n  <div class=\"module\">\n    <div class=\"tile tile--one-half\">One Half</div>\n    <div class=\"tile tile--one-half\">One Half</div>\n  </div>\n  <div class=\"module\">\n    <div class=\"tile tile--one-third\">One Third</div>\n    <div class=\"tile tile--one-third\">One Third</div>\n    <div class=\"tile tile--one-third\">One Third</div>\n  </div>\n  <div class=\"module\">\n    <div class=\"tile tile--one-third\">One Third</div>\n    <div class=\"tile tile--two-thirds\">Two Thirds</div>\n  </div>\n\n  <div slot=\"footer\">\n    <p-tag color=\"background-base\">Footer</p-tag>\n  </div>\n  <div slot=\"sidebar-start\">\n    <p-tag color=\"background-base\">Sidebar</p-tag>\n  </div>\n  <div slot=\"sidebar-end\">\n    <p-tag color=\"background-base\">Sidebar</p-tag>\n  </div>\n</p-canvas>\n\n<script>\n  const canvas = document.querySelector('p-canvas');\n  canvas.addEventListener('dismissSidebarStart', () => (canvas.sidebarStartOpen = false));\n  canvas.addEventListener('dismissSidebarEnd', () => (canvas.sidebarEndOpen = false));\n  document\n    .getElementById('open-sidebar-start')\n    .addEventListener('click', () => (canvas.sidebarStartOpen = !canvas.sidebarStartOpen));\n  document\n    .getElementById('open-sidebar-end')\n    .addEventListener('click', () => (canvas.sidebarEndOpen = !canvas.sidebarEndOpen));\n</script>\n\n<style>\n  body {\n    overflow-x: hidden;\n  }\n\n  p-canvas::part(header) {\n    background: #d1fbc6;\n  }\n\n  p-canvas::part(main) {\n    background: #bed0ff;\n  }\n\n  p-canvas::part(footer) {\n    background: #f7c6fb;\n  }\n\n  p-canvas::part(sidebar-start) {\n    background: #ffbebe;\n  }\n\n  p-canvas::part(sidebar-end) {\n    background: #ffbebe;\n  }\n\n  .module {\n    display: grid;\n    grid-template-columns: subgrid;\n    grid-column: 1 / -1;\n  }\n\n  .tile {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 16px;\n    background: #fff;\n  }\n\n  .tile--full {\n    grid-column: var(--pds-grid-span-full);\n  }\n\n  .tile--one-half {\n    grid-column: var(--pds-grid-span-one-half);\n  }\n\n  .tile--one-third {\n    grid-column: var(--pds-grid-span-one-third);\n  }\n\n  .tile--two-thirds {\n    grid-column: var(--pds-grid-span-two-thirds);\n  }\n</style>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-canvas-example',\n  styles: [\n    `\n      body {\n        overflow-x: hidden;\n      }\n\n      p-canvas::part(header) {\n        background: #d1fbc6;\n      }\n\n      p-canvas::part(main) {\n        background: #bed0ff;\n      }\n\n      p-canvas::part(footer) {\n        background: #f7c6fb;\n      }\n\n      p-canvas::part(sidebar-start) {\n        background: #ffbebe;\n      }\n\n      p-canvas::part(sidebar-end) {\n        background: #ffbebe;\n      }\n\n      .module {\n        display: grid;\n        grid-template-columns: subgrid;\n        grid-column: 1 / -1;\n      }\n\n      .tile {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        padding: 16px;\n        background: #fff;\n      }\n\n      .tile--full {\n        grid-column: var(--pds-grid-span-full);\n      }\n\n      .tile--one-half {\n        grid-column: var(--pds-grid-span-one-half);\n      }\n\n      .tile--one-third {\n        grid-column: var(--pds-grid-span-one-third);\n      }\n\n      .tile--two-thirds {\n        grid-column: var(--pds-grid-span-two-thirds);\n      }\n    `,\n  ],\n  template: `\n    <p-canvas\n      [sidebarStartWidth]=\"'medium'\"\n      [sidebarEndWidth]=\"'medium'\"\n      (dismissSidebarStart)=\"onDismissSidebarStart()\"\n      [sidebarStartOpen]=\"isSidebarStartOpen\"\n      (dismissSidebarEnd)=\"onDismissSidebarEnd()\"\n      [sidebarEndOpen]=\"isSidebarEndOpen\"\n    >\n      <div slot=\"header\">\n        <p-tag color=\"background-base\">Header</p-tag>\n        <p-button-pure icon=\"menu-lines\" (click)=\"onToggleSidebarStart()\">Toggle Sidebar Start</p-button-pure>\n        <p-button-pure icon=\"menu-lines\" (click)=\"onToggleSidebarEnd()\">Toggle Sidebar End</p-button-pure>\n      </div>\n\n      <div class=\"module\">\n        <div class=\"tile tile--full\">Full</div>\n      </div>\n      <div class=\"module\">\n        <div class=\"tile tile--one-half\">One Half</div>\n        <div class=\"tile tile--one-half\">One Half</div>\n      </div>\n      <div class=\"module\">\n        <div class=\"tile tile--one-third\">One Third</div>\n        <div class=\"tile tile--one-third\">One Third</div>\n        <div class=\"tile tile--one-third\">One Third</div>\n      </div>\n      <div class=\"module\">\n        <div class=\"tile tile--one-third\">One Third</div>\n        <div class=\"tile tile--two-thirds\">Two Thirds</div>\n      </div>\n\n      <div slot=\"footer\">\n        <p-tag color=\"background-base\">Footer</p-tag>\n      </div>\n      <div slot=\"sidebar-start\">\n        <p-tag color=\"background-base\">Sidebar</p-tag>\n      </div>\n      <div slot=\"sidebar-end\">\n        <p-tag color=\"background-base\">Sidebar</p-tag>\n      </div>\n    </p-canvas>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class CanvasExampleComponent {\n  isSidebarStartOpen = false;\n  isSidebarEndOpen = false;\n\n  onToggleSidebarStart() {\n    this.isSidebarStartOpen = !this.isSidebarStartOpen;\n  }\n  onDismissSidebarStart() {\n    this.isSidebarStartOpen = false;\n  }\n  onToggleSidebarEnd() {\n    this.isSidebarEndOpen = !this.isSidebarEndOpen;\n  }\n  onDismissSidebarEnd() {\n    this.isSidebarEndOpen = false;\n  }\n}","react":"import { useCallback, useState } from 'react';\nimport { PButtonPure, PCanvas, PTag } from '@porsche-design-system/components-react';\n\nexport const CanvasExamplePage = (): JSX.Element => {\n  const [isSidebarStartOpen, setIsSidebarStartOpen] = useState<boolean>(false);\n  const onToggleSidebarStart = useCallback(() => {\n    setIsSidebarStartOpen((isSidebarStartOpen) => !isSidebarStartOpen);\n  }, []);\n  const onDismissSidebarStart = useCallback(() => {\n    setIsSidebarStartOpen(false);\n  }, []);\n\n  const [isSidebarEndOpen, setIsSidebarEndOpen] = useState<boolean>(false);\n  const onToggleSidebarEnd = useCallback(() => {\n    setIsSidebarEndOpen((isSidebarEndOpen) => !isSidebarEndOpen);\n  }, []);\n  const onDismissSidebarEnd = useCallback(() => {\n    setIsSidebarEndOpen(false);\n  }, []);\n\n  const style = `\n      body {\n        overflow-x: hidden;\n      }\n\n      p-canvas::part(header) {\n        background: #d1fbc6;\n      }\n\n      p-canvas::part(main) {\n        background: #bed0ff;\n      }\n\n      p-canvas::part(footer) {\n        background: #f7c6fb;\n      }\n\n      p-canvas::part(sidebar-start) {\n        background: #ffbebe;\n      }\n\n      p-canvas::part(sidebar-end) {\n        background: #ffbebe;\n      }\n\n      .module {\n        display: grid;\n        grid-template-columns: subgrid;\n        grid-column: 1 / -1;\n      }\n\n      .tile {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        padding: 16px;\n        background: #fff;\n      }\n\n      .tile--full {\n        grid-column: var(--pds-grid-span-full);\n      }\n\n      .tile--one-half {\n        grid-column: var(--pds-grid-span-one-half);\n      }\n\n      .tile--one-third {\n        grid-column: var(--pds-grid-span-one-third);\n      }\n\n      .tile--two-thirds {\n        grid-column: var(--pds-grid-span-two-thirds);\n      }\n  `;\n\n  return (\n    <>\n      <style dangerouslySetInnerHTML={{ __html: style }} />\n      <PCanvas\n        sidebarStartWidth=\"medium\"\n        sidebarEndWidth=\"medium\"\n        onDismissSidebarStart={onDismissSidebarStart}\n        sidebarStartOpen={isSidebarStartOpen}\n        onDismissSidebarEnd={onDismissSidebarEnd}\n        sidebarEndOpen={isSidebarEndOpen}\n      >\n        <div slot=\"header\">\n          <PTag color=\"background-base\">Header</PTag>\n          <PButtonPure icon=\"menu-lines\" onClick={onToggleSidebarStart}>\n            Toggle Sidebar Start\n          </PButtonPure>\n          <PButtonPure icon=\"menu-lines\" onClick={onToggleSidebarEnd}>\n            Toggle Sidebar End\n          </PButtonPure>\n        </div>\n\n        <div className=\"module\">\n          <div className=\"tile tile--full\">Full</div>\n        </div>\n        <div className=\"module\">\n          <div className=\"tile tile--one-half\">One Half</div>\n          <div className=\"tile tile--one-half\">One Half</div>\n        </div>\n        <div className=\"module\">\n          <div className=\"tile tile--one-third\">One Third</div>\n          <div className=\"tile tile--one-third\">One Third</div>\n          <div className=\"tile tile--one-third\">One Third</div>\n        </div>\n        <div className=\"module\">\n          <div className=\"tile tile--one-third\">One Third</div>\n          <div className=\"tile tile--two-thirds\">Two Thirds</div>\n        </div>\n\n        <div slot=\"footer\">\n          <PTag color=\"background-base\">Footer</PTag>\n        </div>\n        <div slot=\"sidebar-start\">\n          <PTag color=\"background-base\">Sidebar</PTag>\n        </div>\n        <div slot=\"sidebar-end\">\n          <PTag color=\"background-base\">Sidebar</PTag>\n        </div>\n      </PCanvas>\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { PButtonPure, PCanvas, PTag } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const isSidebarStartOpen = ref(false);\n  const onToggleSidebarStart = (): void => {\n    isSidebarStartOpen.value = !isSidebarStartOpen.value;\n  };\n  const onDismissSidebarStart = (): void => {\n    isSidebarStartOpen.value = false;\n  };\n\n  const isSidebarEndOpen = ref(false);\n  const onToggleSidebarEnd = (): void => {\n    isSidebarEndOpen.value = !isSidebarEndOpen.value;\n  };\n  const onDismissSidebarEnd = (): void => {\n    isSidebarEndOpen.value = false;\n  };\n</script>\n\n<template>\n  <PCanvas\n    :sidebarStartWidth=\"'medium'\"\n    :sidebarEndWidth=\"'medium'\"\n    @dismissSidebarStart=\"onDismissSidebarStart\"\n    :sidebarStartOpen=\"isSidebarStartOpen\"\n    @dismissSidebarEnd=\"onDismissSidebarEnd\"\n    :sidebarEndOpen=\"isSidebarEndOpen\"\n  >\n    <div slot=\"header\">\n      <PTag color=\"background-base\">Header</PTag>\n      <PButtonPure icon=\"menu-lines\" @click=\"onToggleSidebarStart\">Toggle Sidebar Start</PButtonPure>\n      <PButtonPure icon=\"menu-lines\" @click=\"onToggleSidebarEnd\">Toggle Sidebar End</PButtonPure>\n    </div>\n\n    <div class=\"module\">\n      <div class=\"tile tile--full\">Full</div>\n    </div>\n    <div class=\"module\">\n      <div class=\"tile tile--one-half\">One Half</div>\n      <div class=\"tile tile--one-half\">One Half</div>\n    </div>\n    <div class=\"module\">\n      <div class=\"tile tile--one-third\">One Third</div>\n      <div class=\"tile tile--one-third\">One Third</div>\n      <div class=\"tile tile--one-third\">One Third</div>\n    </div>\n    <div class=\"module\">\n      <div class=\"tile tile--one-third\">One Third</div>\n      <div class=\"tile tile--two-thirds\">Two Thirds</div>\n    </div>\n\n    <div slot=\"footer\">\n      <PTag color=\"background-base\">Footer</PTag>\n    </div>\n    <div slot=\"sidebar-start\">\n      <PTag color=\"background-base\">Sidebar</PTag>\n    </div>\n    <div slot=\"sidebar-end\">\n      <PTag color=\"background-base\">Sidebar</PTag>\n    </div>\n  </PCanvas>\n</template>\n\n<style>\n  body {\n    overflow-x: hidden;\n  }\n</style>\n\n<style scoped>\n  p-canvas::part(header) {\n    background: #d1fbc6;\n  }\n\n  p-canvas::part(main) {\n    background: #bed0ff;\n  }\n\n  p-canvas::part(footer) {\n    background: #f7c6fb;\n  }\n\n  p-canvas::part(sidebar-start) {\n    background: #ffbebe;\n  }\n\n  p-canvas::part(sidebar-end) {\n    background: #ffbebe;\n  }\n\n  .module {\n    display: grid;\n    grid-template-columns: subgrid;\n    grid-column: 1 / -1;\n  }\n\n  .tile {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 16px;\n    background: #fff;\n  }\n\n  .tile--full {\n    grid-column: var(--pds-grid-span-full);\n  }\n\n  .tile--one-half {\n    grid-column: var(--pds-grid-span-one-half);\n  }\n\n  .tile--one-third {\n    grid-column: var(--pds-grid-span-one-third);\n  }\n\n  .tile--two-thirds {\n    grid-column: var(--pds-grid-span-two-thirds);\n  }\n</style>"};
  return samples;
};

type CarouselSampleName = 'example-events' | 'example-dynamic-slides' | 'example-jump-to-slide';
export const getCarouselCodeSamples = (sampleName: CarouselSampleName): FrameworkMarkup => {
  const samples: { [key in CarouselSampleName]: { [key in Framework]?: string } } = {"example-events":{"vanilla-js":"<p-carousel heading=\"Some Heading\">\n  <div>Slide 1</div>\n  <div>Slide 2</div>\n  <div>Slide 3</div>\n</p-carousel>\n\n<p-text>Last event detail: none</p-text>\n\n<script>\n  const carousel = document.querySelector('p-carousel');\n  const debugElement = document.querySelector('p-text');\n\n  carousel.addEventListener('update', (e) => {\n    debugElement.innerText = `Last event detail: ${JSON.stringify(e.detail)}`;\n  });\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\nimport type { CarouselUpdateEventDetail } from '@porsche-design-system/components-angular';\n\n@Component({\n  selector: 'page-carousel-example-events',\n  template: `\n    <p-carousel [heading]=\"'Some Heading'\" (update)=\"onUpdate($event)\">\n      <div>Slide 1</div>\n      <div>Slide 2</div>\n      <div>Slide 3</div>\n    </p-carousel>\n\n    <p-text>Last event detail: {{ lastEventDetail }}</p-text>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class CarouselExampleEventsComponent {\n  lastEventDetail = 'none';\n\n  onUpdate(e: CustomEvent<CarouselUpdateEventDetail>) {\n    this.lastEventDetail = JSON.stringify(e.detail);\n  }\n}","react":"import { useCallback, useState } from 'react';\nimport { type CarouselUpdateEventDetail, PCarousel, PText } from '@porsche-design-system/components-react';\n\nexport const CarouselExampleEventsPage = (): JSX.Element => {\n  const [lastEventDetail, setLastEventDetail] = useState('none');\n  const onUpdate = useCallback(\n    (e: CustomEvent<CarouselUpdateEventDetail>) => setLastEventDetail(JSON.stringify(e.detail)),\n    []\n  );\n\n  return (\n    <>\n      <PCarousel heading=\"Some Heading\" onUpdate={onUpdate}>\n        <div>Slide 1</div>\n        <div>Slide 2</div>\n        <div>Slide 3</div>\n      </PCarousel>\n      <PText children={`Last event detail: ${lastEventDetail}`} />\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { type CarouselUpdateEventDetail, PCarousel, PText } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const lastEventDetail = ref('none');\n  const onUpdate = (e: CarouselUpdateEventDetail): void => {\n    lastEventDetail.value = JSON.stringify(e);\n  };\n</script>\n\n<template>\n  <PCarousel :heading=\"'Some Heading'\" @update=\"onUpdate\">\n    <div>Slide 1</div>\n    <div>Slide 2</div>\n    <div>Slide 3</div>\n  </PCarousel>\n  <PText>Last event detail: {{ lastEventDetail }}</PText>\n</template>"},"example-dynamic-slides":{"vanilla-js":"<p-carousel slides-per-page=\"2\" heading=\"Some Heading\">\n  <div>Slide 1</div>\n  <div>Slide 2</div>\n  <div>Slide 3</div>\n</p-carousel>\n\n<button id=\"btn-add\" type=\"button\">Add slide</button>\n<button id=\"btn-remove\" type=\"button\">Remove last slide</button>\n\n<script>\n  const carousel = document.querySelector('p-carousel');\n\n  document.querySelector('#btn-add').addEventListener('click', () => {\n    const child = document.createElement('div');\n    child.innerText = `Slide ${carousel.children.length + 1}`;\n    carousel.append(child);\n  });\n\n  document.querySelector('#btn-remove').addEventListener('click', () => {\n    carousel.lastChild.remove();\n  });\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-carousel-example-dynamic-slides',\n  template: `\n    <p-carousel [slides-per-page]=\"2\" [heading]=\"'Some Heading'\">\n      <div *ngFor=\"let _ of [].constructor(amountOfSlides); let i = index\">Slide {{ i + 1 }}</div>\n    </p-carousel>\n\n    <button type=\"button\" (click)=\"amountOfSlides = amountOfSlides + 1\">Add slide</button>\n    <button type=\"button\" (click)=\"amountOfSlides = amountOfSlides === 0 ? 0 : amountOfSlides - 1\">\n      Remove last slide\n    </button>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class CarouselExampleDynamicSlidesComponent {\n  amountOfSlides = 3;\n}","react":"import { useCallback, useState } from 'react';\nimport { PCarousel } from '@porsche-design-system/components-react';\n\nexport const CarouselExampleDynamicSlidesPage = (): JSX.Element => {\n  const [amountOfSlides, setAmountOfSlides] = useState(3);\n  const onAddClick = useCallback(() => setAmountOfSlides((prev) => prev + 1), []);\n  const onRemoveClick = useCallback(() => setAmountOfSlides((prev) => (prev === 0 ? 0 : prev - 1)), []);\n\n  return (\n    <>\n      <PCarousel slidesPerPage={2} heading=\"Some Heading\">\n        {Array.from(Array(amountOfSlides)).map((_, i) => (\n          <div key={i} children={`Slide ${i + 1}`} />\n        ))}\n      </PCarousel>\n\n      <button type=\"button\" onClick={onAddClick} children=\"Add slide\" />\n      <button type=\"button\" onClick={onRemoveClick} children=\"Remove last slide\" />\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { PCarousel } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const amountOfSlides = ref(3);\n\n  const onAddClick = (): void => {\n    amountOfSlides.value = amountOfSlides.value + 1;\n  };\n  const onRemoveClick = (): void => {\n    amountOfSlides.value = amountOfSlides.value === 0 ? 0 : amountOfSlides.value - 1;\n  };\n</script>\n\n<template>\n  <PCarousel :slidesPerPage=\"2\" :heading=\"'Some Heading'\">\n    <div v-for=\"(_, i) in Array.from(Array(amountOfSlides))\" :key=\"i\">Slide {{ i + 1 }}</div>\n  </PCarousel>\n\n  <button type=\"button\" @update=\"onAddClick\">Add slide</button>\n  <button type=\"button\" @update=\"onRemoveClick\">Remove last slide</button>\n</template>"},"example-jump-to-slide":{"vanilla-js":"<p-carousel heading=\"Some Heading\" active-slide-index=\"1\">\n  <div>Slide 1</div>\n  <div>Slide 2</div>\n  <div>Slide 3</div>\n</p-carousel>\n\n<button type=\"button\">1</button>\n<button type=\"button\" disabled>2</button>\n<button type=\"button\">3</button>\n\n<script>\n  const carousel = document.querySelector('p-carousel');\n  const buttons = document.querySelectorAll('button');\n\n  const syncButtonsAndCarousel = (newIndex) => {\n    carousel.activeSlideIndex = newIndex;\n    buttons.forEach((el, i) => {\n      el.disabled = newIndex === i;\n    });\n  };\n\n  buttons.forEach((el) => {\n    el.addEventListener('click', (e) => {\n      syncButtonsAndCarousel(parseInt(e.target.innerText) - 1);\n    });\n  });\n\n  carousel.addEventListener('update', (e) => {\n    syncButtonsAndCarousel(e.detail.activeIndex);\n  });\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\nimport type { CarouselUpdateEventDetail } from '@porsche-design-system/components-angular';\n\n@Component({\n  selector: 'page-carousel-example-jump-to-slide',\n  template: `\n    <p-carousel [heading]=\"'Some Heading'\" [activeSlideIndex]=\"activeSlideIndex\" (update)=\"onUpdate($event)\">\n      <div>Slide 1</div>\n      <div>Slide 2</div>\n      <div>Slide 3</div>\n    </p-carousel>\n\n    <button\n      *ngFor=\"let _ of [].constructor(3); let i = index\"\n      type=\"button\"\n      (click)=\"onButtonClick($event)\"\n      [disabled]=\"activeSlideIndex === i\"\n    >\n      {{ i + 1 }}\n    </button>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class CarouselExampleJumpToSlideComponent {\n  activeSlideIndex = 1;\n\n  onUpdate(e: CustomEvent<CarouselUpdateEventDetail>) {\n    this.activeSlideIndex = e.detail.activeIndex;\n  }\n  onButtonClick(e: MouseEvent) {\n    this.activeSlideIndex = parseInt((e.target as HTMLButtonElement).innerText) - 1;\n  }\n}","react":"import { type MouseEvent, useCallback, useState } from 'react';\nimport { type CarouselUpdateEventDetail, PCarousel } from '@porsche-design-system/components-react';\n\nexport const CarouselExampleJumpToSlidePage = (): JSX.Element => {\n  const [activeSlideIndex, setActiveSlideIndex] = useState(1);\n  const onUpdate = useCallback(\n    (e: CustomEvent<CarouselUpdateEventDetail>) => setActiveSlideIndex(e.detail.activeIndex),\n    []\n  );\n  const onButtonClick = useCallback(\n    (e: MouseEvent<HTMLButtonElement> & { target: HTMLButtonElement }) =>\n      setActiveSlideIndex(parseInt(e.target.innerText) - 1),\n    []\n  );\n\n  return (\n    <>\n      <PCarousel heading=\"Some Heading\" activeSlideIndex={activeSlideIndex} onUpdate={onUpdate}>\n        <div>Slide 1</div>\n        <div>Slide 2</div>\n        <div>Slide 3</div>\n      </PCarousel>\n\n      {Array.from(Array(3)).map((_, i) => (\n        <button key={i} type=\"button\" onClick={onButtonClick} disabled={activeSlideIndex === i}>\n          {i + 1}\n        </button>\n      ))}\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { type CarouselUpdateEventDetail, PCarousel } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const activeSlideIndex = ref(1);\n\n  const onUpdate = (e: CarouselUpdateEventDetail): void => {\n    activeSlideIndex.value = e.activeIndex;\n  };\n  const onButtonClick = (e: MouseEvent): void => {\n    activeSlideIndex.value = parseInt((e.target as HTMLButtonElement).innerText) - 1;\n  };\n</script>\n\n<template>\n  <PCarousel :heading=\"'Some Heading'\" :activeSlideIndex=\"activeSlideIndex\" @update=\"onUpdate\">\n    <div>Slide 1</div>\n    <div>Slide 2</div>\n    <div>Slide 3</div>\n  </PCarousel>\n\n  <button\n    v-for=\"(_, i) in Array.from(Array(3))\"\n    :key=\"i\"\n    type=\"button\"\n    @click=\"onButtonClick\"\n    :disabled=\"activeSlideIndex === i\"\n  >\n    {{ i + 1 }}\n  </button>\n</template>"}};
  return samples[sampleName];
};

export const getFlyoutCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"vanilla-js":"<p-button type=\"button\" aria=\"{ 'aria-haspopup': 'dialog' }\">Open Flyout</p-button>\n\n<p-flyout open=\"false\" aria=\"{ 'aria-label': 'Some Heading' }\">\n  <p-heading slot=\"header\" size=\"large\" tag=\"h2\">Some Heading</p-heading>\n  <p-text>Some Content</p-text>\n  <p-button-group slot=\"footer\">\n    <p-button type=\"button\">Proceed</p-button>\n    <p-button type=\"button\" variant=\"secondary\">Cancel</p-button>\n  </p-button-group>\n  <p-text slot=\"sub-footer\">Some additional Sub-Footer</p-text>\n</p-flyout>\n\n<script>\n  const flyout = document.querySelector('p-flyout');\n  flyout.addEventListener('dismiss', () => (flyout.open = false));\n  document.querySelector('p-button').addEventListener('click', () => (flyout.open = true));\n</script>","angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-flyout-example',\n  template: `\n    <p-button type=\"button\" [aria]=\"{ 'aria-haspopup': 'dialog' }\" (click)=\"onOpen()\">Open Flyout</p-button>\n    <p-flyout [open]=\"isFlyoutOpen\" (dismiss)=\"onDismiss()\" [aria]=\"{ 'aria-label': 'Some Heading' }\">\n      <p-heading slot=\"header\" size=\"large\" tag=\"h2\">Some Heading</p-heading>\n      <p-text>Some Content</p-text>\n      <p-button-group slot=\"footer\">\n        <p-button type=\"button\">Proceed</p-button>\n        <p-button type=\"button\" [variant]=\"'secondary'\">Cancel</p-button>\n      </p-button-group>\n      <p-text slot=\"sub-footer\">Some additional Sub-Footer</p-text>\n    </p-flyout>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class FlyoutExampleComponent {\n  isFlyoutOpen = false;\n\n  onOpen() {\n    this.isFlyoutOpen = true;\n  }\n  onDismiss() {\n    this.isFlyoutOpen = false;\n  }\n}","react":"import { useCallback, useState } from 'react';\nimport { PButton, PText, PFlyout, PHeading, PButtonGroup } from '@porsche-design-system/components-react';\n\nexport const FlyoutExamplePage = (): JSX.Element => {\n  const [isFlyoutOpen, setIsFlyoutOpen] = useState<boolean>(false);\n  const onOpen = useCallback(() => {\n    setIsFlyoutOpen(true);\n  }, []);\n  const onDismiss = useCallback(() => {\n    setIsFlyoutOpen(false);\n  }, []);\n\n  return (\n    <>\n      <PButton type=\"button\" aria={{ 'aria-haspopup': 'dialog' }} onClick={onOpen}>\n        Open Modal\n      </PButton>\n      <PFlyout open={isFlyoutOpen} onDismiss={onDismiss} aria={{ 'aria-label': 'Some Heading' }}>\n        <PHeading slot=\"header\" size=\"large\" tag=\"h2\">\n          Some Heading\n        </PHeading>\n        <PText>Some Content</PText>\n        <PButtonGroup slot=\"footer\">\n          <PButton type=\"button\">Proceed</PButton>\n          <PButton type=\"button\" variant=\"secondary\">\n            Cancel\n          </PButton>\n        </PButtonGroup>\n        <PText slot=\"sub-footer\">Some Content</PText>\n      </PFlyout>\n    </>\n  );\n};","vue":"<script setup lang=\"ts\">\n  import { PFlyout, PButton, PHeading, PText, PButtonGroup } from '@porsche-design-system/components-vue';\n  import { ref } from 'vue';\n\n  const isFlyoutOpen = ref(false);\n  const onOpen = (): void => {\n    isFlyoutOpen.value = true;\n  };\n  const onDismiss = (): void => {\n    isFlyoutOpen.value = false;\n  };\n</script>\n\n<template>\n  <PButton type=\"button\" :aria=\"{ 'aria-haspopup': 'dialog' }\" @click=\"onOpen\">Open Flyout</PButton>\n  <PFlyout :open=\"isFlyoutOpen\" :position=\"'end'\" @dismiss=\"onDismiss\">\n    <PHeading slot=\"header\" size=\"large\" tag=\"h2\">Some Heading</PHeading>\n    <PText>Some Content</PText>\n    <PButtonGroup slot=\"footer\">\n      <PButton type=\"button\">Proceed</PButton>\n      <PButton type=\"button\" variant=\"secondary\">Cancel</PButton>\n    </PButtonGroup>\n    <PText slot=\"sub-footer\">Some additional Sub-Footer</PText>\n  </PFlyout>\n</template>"};
  return samples;
};

