export const getStylesHoverCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-styles-hover-example',\n  styles: [\n    `\n      @use '@porsche-design-system/components-angular/styles' as *;\n\n      // Wrapper\n      .wrapper {\n        display: flex;\n        flex-wrap: wrap;\n        justify-content: center;\n        align-items: flex-start;\n        gap: $pds-grid-gap;\n        padding: $pds-spacing-fluid-medium;\n\n        &--light {\n          background: $pds-theme-light-background-base;\n          color: $pds-theme-light-primary;\n        }\n\n        &--dark {\n          background: $pds-theme-dark-background-base;\n          color: $pds-theme-dark-primary;\n        }\n      }\n\n      // Typography\n      .heading {\n        @include pds-heading-medium;\n        text-align: center;\n        width: 100%;\n        margin: 0;\n      }\n\n      // Paragraph\n      .paragraph {\n        @include pds-text-small;\n        margin: 0;\n        max-width: 15rem;\n      }\n\n      // Native Anchor\n      .native-anchor {\n        @include pds-text-small;\n        @include pds-hover;\n        @include pds-focus('none');\n        color: inherit;\n      }\n    `,\n  ],\n  template: `\n    <div class=\"wrapper wrapper--light\">\n      <h3 class=\"heading\">Hover Light</h3>\n      <a href=\"#\" class=\"native-anchor\">Some Anchor</a>\n      <p class=\"paragraph\">\n        Lorem Ipsum\n        <a href=\"#\" class=\"native-anchor\">is simply dummy text of the printing</a> and typesetting industry.\n      </p>\n    </div>\n    <div class=\"wrapper wrapper--dark\">\n      <h3 class=\"heading\">Hover Dark</h3>\n      <a href=\"#\" class=\"native-anchor\">Some Anchor</a>\n      <p class=\"paragraph\">\n        Lorem Ipsum <a href=\"#\" class=\"native-anchor\">is simply dummy text of the printing</a> and typesetting industry.\n      </p>\n    </div>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class StylesHoverExampleComponent {}","react":"import styled from 'styled-components';\nimport {\n  getFocusStyle,\n  getHoverStyle,\n  gridGap,\n  headingMediumStyle,\n  spacingFluidMedium,\n  textSmallStyle,\n  themeDarkBackgroundBase,\n  themeDarkPrimary,\n  themeLightBackgroundBase,\n  themeLightPrimary,\n} from '@porsche-design-system/components-react/styles';\n\n// Wrapper\nconst Wrapper = styled.div({\n  display: 'flex',\n  flexWrap: 'wrap',\n  justifyContent: 'center',\n  alignItems: 'flex-start',\n  gap: gridGap,\n  padding: spacingFluidMedium,\n});\n\nconst WrapperLight = styled(Wrapper)({\n  background: themeLightBackgroundBase,\n  color: themeLightPrimary,\n});\n\nconst WrapperDark = styled(Wrapper)({\n  background: themeDarkBackgroundBase,\n  color: themeDarkPrimary,\n});\n\n// Typography\nconst Heading = styled.h3({\n  ...headingMediumStyle,\n  textAlign: 'center',\n  width: '100%',\n  margin: 0,\n});\n\nconst Paragraph = styled.p({\n  ...textSmallStyle,\n  margin: 0,\n  maxWidth: '15rem',\n});\n\n// Anchor\nconst NativeAnchor = styled.a({\n  ...textSmallStyle,\n  ...getHoverStyle(),\n  ...getFocusStyle({ offset: 'none' }),\n  color: 'inherit',\n});\n\nexport const StylesHoverExample = (): JSX.Element => {\n  return (\n    <>\n      <WrapperLight>\n        <Heading>Hover Light</Heading>\n        <NativeAnchor href=\"#\">Some Anchor</NativeAnchor>\n        <Paragraph>\n          Lorem Ipsum <NativeAnchor href=\"#\">is simply dummy text of the printing</NativeAnchor> and typesetting\n          industry.\n        </Paragraph>\n      </WrapperLight>\n      <WrapperDark>\n        <Heading>Hover Dark</Heading>\n        <NativeAnchor href=\"#\">Some Anchor</NativeAnchor>\n        <Paragraph>\n          Lorem Ipsum <NativeAnchor href=\"#\">is simply dummy text of the printing</NativeAnchor> and typesetting\n          industry.\n        </Paragraph>\n      </WrapperDark>\n    </>\n  );\n};"};
  return samples;
};

