export const getStylesMotionCodeSamples = (): FrameworkMarkup => {
  const samples: { [key in Framework]?: string } = {"angular":"import { ChangeDetectionStrategy, Component } from '@angular/core';\n\n@Component({\n  selector: 'page-styles-motion-example',\n  styles: [\n    `\n      @use '@porsche-design-system/components-angular/styles' as *;\n\n      // Wrapper\n      .wrapper {\n        display: flex;\n        gap: $pds-grid-gap;\n        padding: $pds-spacing-fluid-medium;\n        flex-wrap: wrap;\n        justify-content: center;\n      }\n\n      // Typography\n      .heading {\n        @include pds-heading-medium;\n        color: $pds-theme-light-primary;\n        text-align: center;\n        width: 100%;\n      }\n\n      // Tile\n      .tile {\n        @include pds-text-small;\n        width: 200px;\n        height: 100px;\n        line-height: 100px;\n        text-align: center;\n        color: $pds-theme-light-primary;\n        background: $pds-theme-light-background-surface;\n        border-radius: $pds-border-radius-large;\n        cursor: pointer;\n\n        &--moving {\n          transform: translateX(-200px);\n          transition: transform $pds-motion-duration-short $pds-motion-easing-base;\n\n          &--active {\n            transform: translateX(200px);\n          }\n        }\n\n        &--enter-exit {\n          transform: translateY(0px);\n          transition: opacity $pds-motion-duration-moderate $pds-motion-easing-in,\n            transform $pds-motion-duration-moderate $pds-motion-easing-in;\n\n          &--active {\n            opacity: 0;\n            transform: translateY(40%);\n            transition: opacity $pds-motion-duration-short $pds-motion-easing-out,\n              transform $pds-motion-duration-short $pds-motion-easing-out;\n          }\n        }\n\n        &--show-hide {\n          transition: opacity $pds-motion-duration-long $pds-motion-easing-base;\n\n          &--active {\n            opacity: 0;\n          }\n        }\n\n        &--expand {\n          transition: height $pds-motion-duration-short $pds-motion-easing-in;\n\n          &--active {\n            height: 200px;\n            transition: height $pds-motion-duration-moderate $pds-motion-easing-base;\n          }\n        }\n      }\n    `,\n  ],\n  template: `\n    <div class=\"wrapper\">\n      <h3 class=\"heading\">Moving</h3>\n      <div\n        [ngClass]=\"{ 'tile tile--moving': true, 'tile--moving--active': isMovingActive }\"\n        (click)=\"isMovingActive = !isMovingActive\"\n      >\n        play\n      </div>\n      <h3 class=\"heading\">Enter / Exit</h3>\n      <div\n        [ngClass]=\"{ 'tile tile--enter-exit': true, ' tile--enter-exit--active': isEnterExitActive }\"\n        (click)=\"isEnterExitActive = !isEnterExitActive\"\n      >\n        play\n      </div>\n      <h3 class=\"heading\">Show / Hide</h3>\n      <div\n        [ngClass]=\"{ 'tile tile--show-hide': true, ' tile--show-hide--active': isShowHideActive }\"\n        (click)=\"isShowHideActive = !isShowHideActive\"\n      >\n        play\n      </div>\n      <h3 class=\"heading\">Expand</h3>\n      <div\n        [ngClass]=\"{ 'tile tile--expand': true, ' tile--expand--active': isExpandActive }\"\n        (click)=\"isExpandActive = !isExpandActive\"\n      >\n        play\n      </div>\n    </div>\n  `,\n  changeDetection: ChangeDetectionStrategy.OnPush,\n})\nexport class StylesMotionExampleComponent {\n  isMovingActive = false;\n  isEnterExitActive = false;\n  isShowHideActive = false;\n  isExpandActive = false;\n}","react":"import styled from 'styled-components';\nimport {\n  borderRadiusLarge,\n  gridGap,\n  headingMediumStyle,\n  motionDurationLong,\n  motionDurationModerate,\n  motionDurationShort,\n  motionEasingBase,\n  motionEasingIn,\n  motionEasingOut,\n  spacingFluidMedium,\n  themeLightBackgroundSurface,\n  themeLightPrimary,\n} from '@porsche-design-system/components-react/styles';\nimport { useState } from 'react';\n\n// Wrapper\nconst Wrapper = styled.div({\n  display: 'flex',\n  gap: gridGap,\n  padding: spacingFluidMedium,\n  flexWrap: 'wrap',\n  justifyContent: 'center',\n});\n\n// Typography\nconst Heading = styled.h3({\n  ...headingMediumStyle,\n  color: themeLightPrimary,\n  textAlign: 'center',\n  width: '100%',\n});\n\n// Tile\nconst TileBase = styled.div({\n  width: '200px',\n  height: '100px',\n  lineHeight: '100px',\n  textAlign: 'center',\n  color: themeLightPrimary,\n  background: themeLightBackgroundSurface,\n  borderRadius: borderRadiusLarge,\n  cursor: 'pointer',\n});\n\nconst MotionMoving = styled(TileBase)((props: { active: boolean }) => ({\n  transform: props.active ? 'translateX(200px)' : 'translateX(-200px)',\n  transition: `transform ${motionDurationShort} ${motionEasingBase}`,\n}));\n\nconst MotionEnterExit = styled(TileBase)((props: { active: boolean }) => ({\n  transform: props.active ? 'translateY(40%)' : 'translateY(0px)',\n  transition: props.active\n    ? `opacity ${motionDurationShort} ${motionEasingOut}, transform ${motionDurationShort} ${motionEasingOut}`\n    : `opacity ${motionDurationModerate} ${motionEasingIn}, transform ${motionDurationModerate} ${motionEasingIn}`,\n  ...(props.active && {\n    opacity: '0',\n  }),\n}));\n\nconst MotionShowHide = styled(TileBase)((props: { active: boolean }) => ({\n  transition: `opacity ${motionDurationLong} ${motionEasingBase}`,\n  ...(props.active && {\n    opacity: '0',\n  }),\n}));\n\nconst MotionExpand = styled(TileBase)((props: { active: boolean }) => ({\n  transition: props.active\n    ? `height ${motionDurationModerate} ${motionEasingBase}`\n    : `height ${motionDurationShort} ${motionEasingIn}`,\n  ...(props.active && {\n    height: '200px',\n  }),\n}));\n\nexport const StylesMotionExample = (): JSX.Element => {\n  const [isMovingActive, setIsMovingActive] = useState(false);\n  const [isEnterExitActive, setIsEnterExitActive] = useState(false);\n  const [isShowHideActive, setIsShowHideActive] = useState(false);\n  const [isExpandActive, setIsExpandActive] = useState(false);\n\n  return (\n    <>\n      <Wrapper>\n        <Heading>Moving</Heading>\n        <MotionMoving active={isMovingActive} onClick={() => setIsMovingActive((prev) => !prev)}>\n          play\n        </MotionMoving>\n        <Heading>Enter / Exit</Heading>\n        <MotionEnterExit active={isEnterExitActive} onClick={() => setIsEnterExitActive((prev) => !prev)}>\n          play\n        </MotionEnterExit>\n        <Heading>Show / Hide</Heading>\n        <MotionShowHide active={isShowHideActive} onClick={() => setIsShowHideActive((prev) => !prev)}>\n          play\n        </MotionShowHide>\n        <Heading>Expand</Heading>\n        <MotionExpand active={isExpandActive} onClick={() => setIsExpandActive((prev) => !prev)}>\n          play\n        </MotionExpand>\n      </Wrapper>\n    </>\n  );\n};"};
  return samples;
};

